@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&family=Satisfy&family=Varela+Round&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@keyframes fadeRightToLeft {
  0% {
    opacity: 0;
    transform: translateX(100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes fadeInToOut {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
html {
  font-size: 62.5%;
  -webkit-tap-highlight-color: transparent;
}

body {
  box-sizing: border-box;
  font-size: 1.4rem;
  line-height: 1.6;
  font-family: "Varela Round";
  background-color: #272b34;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

ul {
  list-style: none;
}

a {
  display: block;
  text-decoration: none;
  color: inherit;
}

li, i {
  cursor: pointer;
}

img {
  max-width: 100%;
  display: block;
  object-fit: cover;
  object-position: center;
}

input {
  outline: none;
  border: none;
}

.logo {
  font-size: 3rem;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.7);
  font-weight: bold;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 140px;
  height: 40px;
  color: rgba(255, 255, 255, 0.8);
  background-color: #fe8033;
  border-radius: 10px;
  position: relative;
  cursor: pointer;
  overflow: hidden;
}
.btn span {
  z-index: 1;
}

.title_page {
  color: #fff;
  font-size: 4rem;
}

.container {
  max-width: 1400px;
  margin: auto;
  margin-top: 100px;
  padding: 20px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -12px;
  margin-right: -12px;
}

.row.no-gutters {
  margin-left: 0;
  margin-right: 0;
}

.col {
  padding-left: 12px;
  padding-right: 12px;
  margin-bottom: 20px;
}

.row.no-gutters .col {
  padding-left: 0;
  padding-right: 0;
}

