.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100px;
  background-color: #272b34;
  box-shadow: 0px 1px 4px 0px #272b34;
  padding: 0 30px;
  z-index: 999;
}
.header .menu {
  font-size: 2.2rem;
  color: rgba(255, 255, 255, 0.7);
  display: none;
}
.header .menu:hover {
  color: #fff;
}
.header .active_menu {
  visibility: visible;
  transform: translateY(0);
  opacity: 1;
}
.header ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header ul li {
  margin-left: 15px;
  padding: 10px;
  color: rgba(255, 255, 255, 0.7);
  transition: 0.2s ease;
  font-size: 1.8rem;
}
.header ul li:hover {
  color: #fff;
}
.header ul li a {
  white-space: nowrap;
}
.header .search {
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgba(255, 255, 255, 0.7);
  font-size: 2rem;
}
.header .search .search_input {
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 0px 7px -1px #ffffff2a;
  border-radius: 20px;
  min-height: 40px;
  overflow: hidden;
}
.header .search .search_input .icon-search {
  margin: 0;
  cursor: default;
  padding-left: 15px;
}
.header .search .search_input .input {
  padding: 0 15px;
  background-color: #272b34;
  color: #fff;
  font-size: 1.4rem;
}
.header .search .search_input .input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.5rem;
  font-weight: 600;
}
.header .search .search_input .input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.5rem;
  font-weight: 600;
}
.header .search .search_input .input::placeholder {
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.5rem;
  font-weight: 600;
}
.header .search .icon-cart {
  position: relative;
  margin-left: 25px;
  transition: 0.1s ease;
}
.header .search .icon-cart .icon-cart-quantity {
  font-size: 1rem;
  color: #fff;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(50%, -50%);
  width: 15px;
  height: 15px;
  line-height: 15px;
  border-radius: 100%;
  background-color: #fe8033;
  text-align: center;
}
.header .search .icon-cart:hover {
  color: #fff;
}
.header .active {
  color: #fff;
}

.product {
  padding-top: 50px;
  position: relative;
  z-index: 9;
}
.product .product_title {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  color: #2d343e;
  font-size: 10.5vw;
  z-index: 0;
}
.product .product_card {
  max-width: 300px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  border-radius: 15px;
  background-color: #fff;
  text-align: center;
  overflow: hidden;
  transition: 0.2s ease-out;
  cursor: pointer;
  z-index: 9;
}
.product .product_card .card_img img {
  max-height: 300px;
}
.product .product_card .card_header {
  width: 100%;
  margin: 25px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
  flex-direction: column;
}
.product .product_card .card_header .product_name {
  word-wrap: break-word;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  font-size: 1.8rem;
}
.product .product_card .card_header .price {
  font-size: 2.5rem;
  color: #fe8033;
  margin: 10px 0;
}
.product .product_card .card_header .btn:hover::before {
  width: 350px;
  height: 150px;
}
.product .product_card .card_header .btn::before {
  content: "";
  position: absolute;
  background-color: rgba(224, 189, 125, 0.664);
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  border-radius: 100%;
  transition: 0.3s ease-in-out;
  transform: translate(-50%, -50%);
}
.product .product_card:hover {
  transform: scale(1.1);
}

.footer {
  margin-top: 100px;
  background-color: #2b3039;
  padding: 50px 0;
  width: 100%;
}
.footer .container {
  margin: 0 auto;
}
.footer .footer_title {
  font-size: 2.2rem;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 10px;
}
.footer .footer_list {
  font-size: 1.6rem;
  color: rgba(255, 255, 255, 0.4);
  display: flex;
  flex-direction: column;
}
.footer .footer_list .footer_item {
  margin-bottom: 5px;
  transition: 0.2s ease;
}
.footer .footer_list .footer_item a {
  display: inline-block;
  position: relative;
}
.footer .footer_list .footer_item a::before {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  width: 0;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.7);
  transition: 0.2s linear;
}
.footer .footer_list .footer_item:hover a::before {
  width: 100%;
}
.footer .footer_list .footer_item:hover {
  color: #fff;
}

.cover {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  background-color: rgba(51, 51, 51, 0.185);
  animation: fadeInToOut 0.4s ease;
}

.cart {
  position: absolute;
  right: 0;
  height: 100%;
  background-color: #fff;
  overflow: scroll;
  padding: 40px;
  max-width: 423px;
  width: 100%;
  color: #2d343e;
  box-shadow: -2px 0px 2px 0px rgba(0, 0, 0, 0.034);
  animation: fadeRightToLeft 0.4s linear;
}
.cart .cart_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.8rem;
  padding-bottom: 20px;
  border-bottom: 1px solid #4a4a4a29;
}
.cart .cart_header .cart_close {
  font-size: 2rem;
}
.cart .cart_item {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 30px 0;
  cursor: default;
  border-bottom: 1px solid #4a4a4a29;
}
.cart .cart_item .cart_item-img {
  width: 30%;
}
.cart .cart_item .cart_item-img img {
  width: 250px;
  border-radius: 2px;
}
.cart .cart_item .cart_item-info {
  width: 70%;
  margin-left: 25px;
  padding: 0 5px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  overflow: hidden;
}
.cart .cart_item .cart_item-info .cart_item-name {
  width: 100%;
  font-size: 2rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 5px;
}
.cart .cart_item .cart_item-info .cart_item-modify {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.cart .cart_item .cart_item-info .cart_item-modify .cart_item-quantity {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  border: 1px solid #4a4a4a29;
}
.cart .cart_item .cart_item-info .cart_item-modify .cart_item-quantity i {
  padding: 2px 5px;
}
.cart .cart_item .cart_item-info .cart_item-modify .cart_item-close {
  display: flex;
  justify-content: center;
  align-items: center;
}
.cart .cart_item .cart_item-info .cart_item-modify .cart_item-close i {
  margin-right: 12px;
  color: #cf0909;
}
.cart .cart_item .cart_item-info .cart_item-modify .cart_item-close .total {
  font-weight: bold;
}
.cart .cart_item .quantity {
  border-left: 1px solid #4a4a4a29;
  border-right: 1px solid #4a4a4a29;
  padding: 0 10px;
}
.cart .cart_pay {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 30px 0;
}
.cart .cart_pay .pay_text {
  text-transform: uppercase;
}
.cart .cart_pay .pay_total {
  font-size: 1.8rem;
  font-weight: bold;
}
.cart .empty {
  font-size: 1.8rem;
  padding: 20px 0;
}
.cart .btn_pay {
  width: 100%;
  position: relative;
  text-transform: uppercase;
}
.cart .btn_pay span {
  position: relative;
  z-index: 2;
}
.cart .btn_pay::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  height: 0;
  width: 100%;
  background-color: #016097;
  transform: translate(-50%, -50%) rotate(-45deg);
  transition: 0.2s linear;
  z-index: 1;
}
.cart .btn_pay:hover::before {
  height: 630%;
  color: #fff;
}

.prewiew {
  position: relative;
}
.prewiew .preview_desc {
  max-width: 50%;
  margin-left: 50px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  color: rgba(255, 255, 255, 0.7);
}
.prewiew .preview_title {
  font-size: 5.9rem;
}
.prewiew .preview_text {
  width: 80%;
  opacity: 0.5;
  padding-bottom: 35px;
}
.prewiew .preview_button {
  display: flex;
  justify-content: center;
  align-items: center;
}
.prewiew .preview_button .btn {
  white-space: nowrap;
  border-radius: 35px;
  padding: 25px 60px;
  transition: 0.2s ease-in;
  font-weight: bold;
}
.prewiew .preview_button .btn_buynow {
  margin-right: 30px;
}
.prewiew .preview_button .btn_buynow:hover {
  color: #fff;
  background-color: #fb863e;
}
.prewiew .preview_button .btn_explore {
  background-color: transparent;
  border: 1px solid #fe8033;
}
.prewiew .preview_button .btn_explore:hover {
  background-color: #fe8033;
}
.prewiew .preview_img {
  position: absolute;
  top: 0;
  right: 0%;
  transform: scale(1.3);
}

